import React from "react";
import "./preview.component.css";

const Preview = ({ visible }) => {
    const picturesrc = visible ? document.getElementById("passportImg").src : null;
    
    const Print = () => {
        const pwin = window.open("Passport For Printing");
        //for (let i = 0; i < 3; i++) {
        //    for (let j = 0; j < 5; j++) {
                pwin.document.write(`<img
                src={${picturesrc}} onload="window.print();window.close()"/>`);
        //    }
        //}
        pwin.focus();
    }

    if (visible)
        return (
            <div id="preview">
                <button
                    id="print-btn"
                    className="btn"
                    onClick={() => Print()}>Print/Save</button>
            </div>
        );
    return (<div />);
}

export default Preview;
